import utlis from '../../static/js/utlis.js';
export default {
	data() {
		return {
			tab: 0,
			prompt: '手机号输入有误，请重新输入',
			showPrompt: 0,
			code: '获取验证码',
			disabled: true,
			phoneNum: '',
			password: '',
			smsCode: '',
			appCust: ''
		}
	},
	onLoad() {

	},
	methods: {
		onKeyInputPhone: function(event) {
			this.phoneNum = event.target.value
			// console.log(this.phoneNum)
		},
		onKeyInputCode: function(event) {
			this.smsCode = event.target.value
			// console.log(this.smsCode)
		},
		onKeyInputPsd: function(event) {
			this.password = event.target.value
			// console.log(this.password)
		},
		// 获取验证码
		reCode() {
			let self = this;
			if (!utlis.checkMobile(this.phoneNum)) {
				uni.showToast({
					title: '手机号输入有误，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			var time = 60
			if (self.code == "获取验证码" || self.code == "重新获取") {
				self.code = time + "s后重获"
				let data = {
					phone: self.phoneNum,
				}
				uni.showLoading({
					title: '加载中',
					mask: true
				})
				// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/smsSend', data, function(res) {
				utlis.http("POST", 'smsSend', data, function(res) {
					if (res.status == "success") {
						uni.hideLoading()
						uni.showToast({
							title: res.msg,
							duration: 2000,
							mask: true,
							icon: 'success'
						})
					} else {
						// 验证提示
						uni.hideLoading()
						uni.showToast({
							title: res.msg,
							duration: 2000,
							mask: true,
							icon: 'none'
						})
					}
				})
				var verification = setInterval(() => {
					time--;
					self.code = time + "s后重获";
					if (time == 0) {
						self.code = "重新获取";
						time = 60;
						clearInterval(verification)
					}
				}, 1000);
			}
		},
		// 提交
		showPro() {
			let self = this;
			if (!utlis.checkMobile(this.phoneNum)) {
				uni.showToast({
					title: '手机号输入有误，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.password == '') {
				uni.showToast({
					title: '密码不能为空',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			let data = {
				custName: self.phoneNum,
				password: self.password
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appLoginHj', data, function(res) {
			utlis.http("POST", 'appLoginHj', data, function(res) {
				if (res.status == "success") {
					uni.setStorageSync("token", res.token)
					uni.setStorageSync("refreshToken", res.refreshToken)
					uni.setStorageSync('phoneNum', self.phoneNum)
					uni.setStorageSync('custName', self.phoneNum)
					uni.setStorageSync('password', self.password)
					uni.setStorageSync("egCode", res.egCode)
					self.appCust = res.appCust
					// 登录提示
					uni.hideLoading()
					uni.showToast({
						title: res.msg,
						duration: 2000,
						mask: true,
						icon: 'success'
					})
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}, 2000)
				} else {
					// 登录提示
					uni.hideLoading()
					uni.showToast({
						title: res.msg,
						duration: 2000,
						mask: true,
						icon: 'none'
					})
				}
			})
		},
		//注册提交
		showProRegister() {
			let self = this;
			if (!utlis.checkMobile(this.phoneNum)) {
				uni.showToast({
					title: '手机号输入有误，请重新输入',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.smsCode == '') {
				uni.showToast({
					title: '验证码不能为空',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			if (this.password == '') {
				uni.showToast({
					title: '密码不能为空',
					duration: 2000,
					mask: true,
					icon: 'none'
				})
				return;
			}
			let data = {
				forget: 0,
				custName: self.phoneNum,
				smsCode: self.smsCode,
				password: self.password,
				userSource: 1,
				xmType: 1
			}
			uni.showLoading({
				title: '加载中',
				mask: true
			})
			// utlis.http("POST", 'https://www.hjexyz.cn/lcyj-common-wap/newApp/appRegister', data, function(res) {
			utlis.http("POST", 'appRegister', data, function(res) {
				if (res.status == "success") {
					uni.setStorageSync("token", res.token)
					uni.setStorageSync("refreshToken", res.refreshToken)
					uni.setStorageSync('phoneNum', self.phoneNum)
					uni.setStorageSync('custName', self.phoneNum)
					// 注册提示
					uni.hideLoading()
					uni.showToast({
						title: res.msg,
						duration: 2000,
						mask: true,
						icon: 'success'
					})
					setTimeout(() => {
						uni.reLaunch({
							url: '/pages/index/index'
						})
					}, 2000)
				} else {
					// 注册提示
					uni.hideLoading()
					uni.showToast({
						title: res.msg,
						duration: 2000,
						mask: true,
						icon: 'none'
					})
				}
			})

		},
		changeTab(e) {
			this.tab = e.target.dataset.index
		},
		forgetPsd(e) {
			uni.navigateTo({
				url: '/pages/forget/forget'
			})
		},
		cancel(e) {
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
	}
}
